import Vue from 'vue'
import './registerServiceWorker'
import VueAMap from 'vue-amap';
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import App from './App'
import router from './router'
import store from './store/store'
import {stamp} from './utils/utils'
import api from './http/index' // 导入api接口

Vue.use(ElementUI);
Vue.config.productionTip = false

//注册地图
Vue.use(VueAMap);
VueAMap.initAMapApiLoader({
  key: 'aca0802c1fe6ef6f5834077344b1d0dc',
  plugin: ['AMap.Geolocation','AMap.PolyEditor','AMap.PlaceSearch'],
  v: '1.4.4'
})

// 全局调用
Vue.prototype.$stamp = stamp;
Vue.prototype.$api = api; // 将api挂载到vue的原型上

//全局自定义获取焦点方法
Vue.directive('focus', function (el) {
  el.querySelector('input').focus()
})

//全局防止按钮双击重复提交 全部点击按钮都是button标签 因为有disable值进行控制 兼容性好
Vue.directive('dbClick', {
  inserted(el, binding) {
    el.addEventListener('click', e => {
      if(!el.disabled) {
        el.disabled = true;
        let timer = setTimeout( () => {
          el.disabled = false;
        },1000)
      }
    })
  }
})

new Vue({
  router,
  store,
  render: function (h) { return h(App) }
}).$mount('#app')
